@import './Global.scss';


.img-wrap {
  text-align: center;
  position: relative;
  margin-top:30px;
  overflow: hidden;
   @include breakpoint("desktop"){
    &:hover .overlay{
      height: 20%;
    }
    &:hover .text{
      margin-top:0px;
    }
    
   }
  .img {
    width: 100%;
    height: auto;
    transition: transform 0.5 ease-in-out;
  }
  .overlay {
    position: absolute;
    bottom:0;
    background-color:rgba($primaryColor,0.6);
    width: 100%;
    height: 30%;
    @include breakpoint("desktop"){
      height: 100%;
      transition: height 0.6s;
    }
  }

  .text {
    color: white;
    text-align: center;
    padding: 0.2em;
    @include breakpoint("desktop"){
      display: block;
      margin-top:200px;
    }
    h4{
      font-weight: 700;
      font-size: clamp(16px, 2vw, 21px);
    }
    P{
      font-weight: 400;
      font-size: clamp(14px, 2vw, 18px);
      @include breakpoint("tablet"){
        margin-top:10px;
      }
    }
  }
}
.subtitle{
  font-weight: 300;
  font-size: clamp(0.9rem, 2.5vw,1.2rem);
}


